import {useState, useEffect} from 'react';
import twitterThread from './twitter Thread Cloner.png'
import homeStyles from './home.module.css';
import crypto from 'crypto';
import axios from "axios";
import socketIOClient from "socket.io-client";
import HomeBody from "./homeBody";
import Loading from "./loading";
import Download from "./download";


const Home = () => {
    const [url, setUrl] = useState("");
    const [loading, setLoading] = useState(false);
    const [downloadLink, setDownloadLink] = useState(false);
    const [status, setStatus] = useState("");
    const socket = socketIOClient("http://localhost:4000");
    useEffect(() => {
        socket.on("statusCode", (code) => {
            setStatus(code);
        });
    }, []);



    const urlFieldChangeHandler = (e) => {
        setUrl(e.target.value)
    }
    const createPDF = (e) => {
        e.preventDefault();
        setLoading(true);
        axios.post('/convert/pdf', {
            url: url,
        })
            .then((res) => {
                console.log(res);
                setDownloadLink(res.data);
                setLoading(false);
            })
            .catch(e => console.log(e));
    }
    const createTXT = (e) => {
        e.preventDefault();
        setLoading(true);
        axios.post('/convert/txt', {
            url: url,
        })
            .then((res) => {
                console.log(res);
                setDownloadLink(res.data);
                setLoading(false);
            })
            .catch(e => console.log(e));
    }
    const createPPT = (e) => {
        e.preventDefault();
        axios.post('/convert/ppt', {
            url: url
        })
            .then((res) => {
                console.log(res.data);
            })
            .catch(e => console.log(e));
    }
    return (
        <div className={homeStyles.homeContainer}>
            <div className={homeStyles.homeNavBarContainer}>
                <img src={twitterThread} alt={"ttc logo"} className={homeStyles.ttcLogoImage} />
            </div>
            {(loading)?
                <Loading status={status} />:
                (downloadLink)?
                    <Download downloadLink={downloadLink} />:
                    <HomeBody createPDF={createPDF} createPPT={createPPT} createTXT={createTXT} urlFieldChangeHandler={urlFieldChangeHandler} />}

            <div className={homeStyles.homeFooter}>
                <div className={homeStyles.footerContent}>
                    <span className={homeStyles.developedByText}>Developed By</span>
                    <a href={'https://github.com/Iam-VM/'} className={'link'} ><span className={homeStyles.developerName}>Vishnu Mohanan</span></a>
                </div>
            </div>
        </div>
    )
};

export default Home;
