import homeStyles from "./home.module.css";
import crypto from "crypto";

const HomeBody = (props) => {
    return (
        <div className={homeStyles.homeBody}>
            {/*{(file)?<span>file arrived</span>:null}*/}
            <span className={homeStyles.homeDescription}>Clone twitter threads into PDF, Text or PPT</span>
            <input placeholder={"Paste thread URL here..."} onChange={(e) => props.urlFieldChangeHandler(e)} type="text" name={crypto.randomBytes(16)} size={60} className={homeStyles.homeUrlTextField} />
            <div className={homeStyles.homeButtonsContainer}>
                <button onClick={(e) => {props.createPDF(e)}} className={`${homeStyles.homeConvertButton} ${homeStyles.button1}`}>Covert to PDF</button>
                <button onClick={(e) => {props.createTXT(e)}} className={`${homeStyles.homeConvertButton} ${homeStyles.button2}`}>Covert to txt</button>
                <button onClick={(e) => {props.createPPT(e)}} className={`${homeStyles.homeConvertButton} ${homeStyles.button3}`}>Covert to PPT</button>
            </div>
        </div>
    )
};

export default HomeBody;
